import React, { useState } from 'react'
import './Header.css'
import { NavLink } from "react-router-dom";




function Header() {
  const [cName, setCName] = useState(false)
  const handleEvent = ()=>{
  setCName(!cName);
  
  }
 
    return (


        <div className= {cName?"header":"header res"}>
        
        <NavLink to="" exact activeClassName="current" className="logo">
        
        <p>Logo</p>
      </NavLink>

      <div className ={cName?"header_content ": "header_content responsive" } >
      
      <NavLink to="" activeClassName="current" className ="header_item">
        
        <p>Home</p>
      </NavLink>
      <NavLink to="" activeClassName="current" className ="header_item">
        
        <p>About Us</p>
      </NavLink>
      <NavLink to="" activeClassName="current" className ="header_item">
      
        <p>Courses</p>
      </NavLink>
      <NavLink to="" activeClassName="current" className ="header_item">
      
        <p>Contact Us</p>
      </NavLink>
     
      
      
      </div> 
      <div className = "icon" onClick = {handleEvent} >
      <NavLink to="" >
      
      <p>Icon</p>
    </NavLink>
    </div>


        </div>

    )
}

export default Header
