import React from "react";
import "./Future.css";
function Future() {
    var acc = document.getElementsByClassName("accordion");
var i;

for (i = 0; i < acc.length; i++) {
  acc[i].addEventListener("click", function() {
    this.classList.toggle("active");
    var panel = this.nextElementSibling;
    if (panel.style.display === "block") {
      panel.style.display = "none";
    } else {
      panel.style.display = "block";
    }
  });
}
  return (
    <div>
      <div className="future">
        <div className="future_inner">
          <div className="future_heading">
            How Wisdom Academy would help you for your future educational
            endeavors
          </div>
          <div className="future_description">
            <ul className="future_description_list">
              <li>
                Wisdom Academy in Mumbai is known for being an expert coach or
                mentor for students in engineering courses like Information
                Technology, Computer Science Engineering, Civil Engineering,
                Mechanical Engineering, Electronic andTele-Communication (EXTC).
              </li>
              <li>
                If you are passionate about learning and education Wisdom
                Academy is the right place for you.
              </li>
              <li>
                Our experienced and professional tutors help implement our
                strategy to educate students in an interactive manner which
                helps them visualize even the most difficult concepts.
              </li>
              <li>
                Our state of the art facilities and faculties make sure you
                become an expert in the course you select as we recognize the
                importance of quality education because the concepts which are
                learned by an engineer during his engineering has real-life
                practical implications and uses on live projects.
              </li>
              <li>
                Wisdom Academy, we believe in nurturing students to the level
                where they are capable enough to practically implement the
                concepts they are learning in classrooms We provide engineering
                classes for all courses
              </li>
              <li>
                Contact our nearest branch for admissions and inquiry at our
                engineering classes in Andheri, Borivali, and Mumbai
              </li>
              <li style={{ "font-weight": "800" }}>
                Note: – “Wisdom Academy Provides Group Tuitions (5-10 Students
                only) at Andheri/Borivali centers and also Home Tuitions (All
                Over Mumbai) for all branches and all subjects.”
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Future;
