import React from 'react'
import Achievement from './Achievement'
import './Testimonial.css'
import CountUp from 'react-countup';

function Testimonial() {
    
    return (
        <div className = "testimonial">
        <div className = "title">
        <h1>OUR ACHIEVEMENTS</h1>
        <h3>We have achieved a lot with the help of our students and aim to achieve more.</h3>
        </div>
        <div className="testimonial_item">
        <div className = "achivement">
    <Achievement
    title = "Computer Science And Engineering"
    number = {98}
    />
    <Achievement
    title = "Electronics and Communication Engineering"
    number = {87}
    />
    <Achievement
    title = "Electrical Engineering"
    number = {80}
    />
    <Achievement
    title = "Mechanical Engineering"
    number = {92}
    /> 
    <Achievement
    title = "Civil Engineering"
    number = {89}
    />
    </div>
    <div className = "count_up">
    <CountUp start={0} end={6547} duration={10}/>
    <div className = "title">
        <h1>OUR STUDENTS</h1>
        <h3>Count of students who have come out with flying colours.</h3>
        </div>
    </div>
    </div>

        </div>
    )
}

export default Testimonial
